







































.app {
  display: flex;
  min-width: 800px;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 40px;
  background-color: var(--gray--900);
}

.app__container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr;
}

.app__footer {
  color: var(--white);
  grid-column: 1 / -1;
  text-align: center;
}
